// slider
.slider_container {
    height: 45vh;
    width: 100%;
    // border: 1px solid black;
    overflow: hidden;
    position: relative;
    margin: auto;
}

.slider_container_error{
    display: none;
}

.active {
    display: inline-block;
}

.inactive {
    display: none;
}

.slides {
    height: 45vh;
    width: 100%;
    position: relative;
}

.slide_image {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
    opacity: 0.9;
}

.slide_prev, .slide_next {
    cursor: pointer;
    z-index: 100;
    position: absolute;
    top: 50%;
    width: auto;
    padding: 1rem;
    margin-top: -3rem;
    font-size: 30px;
    border-radius: 0 5px 5px 0;
    font-weight: bold;
}

.slide_prev:hover, .slide_next:hover {
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.6);
    transition: all 0.5s ease-in;
}

.slide_next {
    right: 0;
    border-radius: 5px 0 0 5px;
}

.slide_prev {
    left: 0;
    border-radius: 5px 0 0 5px;
}

.slide_all_dots {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    top: 85%;
    justify-content: center;
    z-index: 200;
}

.dot {
    cursor: pointer;
    height: 1rem;
    width: 1rem;
    margin: 0 3px;
    // border-color: rgba(0, 0, 0, 0.3);
    background-color: rgba(75, 72, 72, 0.3);
    border-radius: 50%;
    display: inline-block;
}

.active-dot, .dot:hover {
    background-color: rgba(255, 255, 255, 0.5);
}

.homeComponent {
    width: 100%;
    height: 50vh;
    text-align: center;
    h1 {
        line-height: 50vh;
    }
}

@include breakpint(mobile) {
    .slider_container {
        display: none;
    }
}